import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EnterComponent } from './components/client/enter/enter.component';
import { OutComponent } from './components/client/out/out.component';
import { NewPasswordComponent } from './components/forgot/new-password/new-password.component';
import { SendForgotComponent } from './components/forgot/send-forgot/send-forgot.component';
import { HomeLiveComponent } from './components/home/home-disconnected/home-live.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { AuthGuardService } from './services/auth/auth-guard.service';




const routes: Routes = [
  {
    path: '',
    component: EnterComponent
  },
  {
    path: 'bye',
    component: OutComponent,
    canActivate: [AuthGuardService]
  },

  {
    path: 'motdepasseoublie',
    component: SendForgotComponent
  },
  {
    path: 'reinitialisation',
    component: NewPasswordComponent
  },
  {
    path: 'accueil',
    component: HomeLiveComponent
  },
  {
    path: 'client',
    loadChildren: () => import('./components/client/client-routing.module').then(m => m.ClientRoutingModule)
  },
  {
    path: 'modele',
    loadChildren: () => import('./components/modele/model-routing.module').then(m => m.ModelRoutingModule)
  },
  {
    path: '**',
    component: PageNotFoundComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled', relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
